body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-gradient(linear, right top, left top, from(rgba(7,27,82,1)), to(rgba(0,128,128,1)));
  background: -webkit-linear-gradient(right, rgba(7,27,82,1) 0%, rgba(0,128,128,1) 100%);
  background: linear-gradient(to left, rgba(7,27,82,1) 0%, rgba(0,128,128,1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-display: swap;
    font-family: 'SEGA LOGO FONT';
    font-style: normal;
    font-weight: normal;
    src: local('SEGA LOGO FONT'), url(/course--complete-senior-web-developer/static/media/SEGA.12a3db8b.woff) format('woff');
    }
    
    h1 {
      font-family: 'SEGA LOGO FONT';
      font-weight: 200;
      color: #0ccac4;
    }
